import { createSlice } from '@reduxjs/toolkit';
//     user   
import { IUserData } from './types/authType';
import { getUserWithToken, loginUser } from './authAction';

//  state
interface IUserState {
  user:IUserData
  isLoading: boolean
  error: string
}

//    user
const initialUser: IUserData = {
  id: 0,
  username: '',
  email: '',
  firstName: '',
  lastName: '',
  gender: '',
  image: '',
  token: '',
  refreshToken: ''
}

//  state     user
const initialState: IUserState = {
  user: initialUser,
  isLoading: false,
  error: '',
};

//  slice
export const authSlice = createSlice({
  //   slice
  name: 'authSlice',
  //   state
  initialState,
  reducers: {
    //   action   state
    logoutUser: (state) => {
      state.user = initialUser
    }
  },
  // !     
  extraReducers: (builder) => {
    builder
      .addCase(loginUser.pending, (state) => {
        state.isLoading = true;
      })
      .addCase(loginUser.fulfilled, (state, action) => {
        state.isLoading = false
        state.user = action.payload;
      })
      .addCase(loginUser.rejected, (state, action) => {
        state.isLoading = false
        state.user = initialUser
        state.error = action.payload as string
      })
      .addCase(getUserWithToken.fulfilled, (state, action) => {
        state.isLoading = false
        state.user = action.payload;
      })
      .addCase(getUserWithToken.pending, (state, action) => {
        state.isLoading = false
        
      })
  },
});

export default authSlice;
//   actions  slice
export const { logoutUser } = authSlice.actions
