// src/components/articles/Articles.tsx
import React, { useState, useEffect } from 'react';
import axios from 'axios';
import ArticleForm from './ArticleForm';
import ArticleList from './ArticleList';
import styles from './articles.module.css';

//    
interface Article {
  id: number;
  title: string;
  content: string;
  categoryId: number;
}

const Articles = () => {
  const [articles, setArticles] = useState<Article[]>([]); //   Article   

  useEffect(() => {
    fetchArticles();
  }, []);

  //    
  const fetchArticles = async () => {
    try {
      const response = await axios.get<Article[]>('/api/articles');
      setArticles(response.data);
    } catch (error) {
      console.error('   :', error);
    }
  };

  //    
  const handleCreate = async (data: { title: string; content: string; categoryId: number }) => {
    try {
      await axios.post('/api/articles', data);
      fetchArticles();
    } catch (error) {
      console.error('   :', error);
    }
  };

  //    
  const handleEdit = (article: Article) => {
    //       
    //   article  ArticleForm  
    console.log(' :', article);
  };

  //    
  const handleDelete = async (id: number) => {
    try {
      await axios.delete(`/api/articles/${id}`);
      fetchArticles();
    } catch (error) {
      console.error('   :', error);
    }
  };

  return (
    <div className={styles.articlesContainer}>
      <ArticleForm onSubmit={handleCreate} />
      <ArticleList articles={articles} onEdit={handleEdit} onDelete={handleDelete} />
    </div>
  );
};

export default Articles;
